import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import ContactForm from "@screens/contactus/contactform";

const DefiDexDevelopment = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Defi Dex Aggregator Platform Development", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <ContactForm db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const logoObj = data?.allImageSharp?.edges?.find((image) => {
    return image?.node?.gatsbyImageData?.images?.fallback?.src?.includes(
      "new_logo.png"
    );
  });
  return (
    <Seo
      title="Defi Dex Aggregator Platform Development | Justtry Technologies"
      metaTitle="Defi Dex Aggregator Platform Development | Justtry Technologies"
      description="Build a DeFi DEX aggregator platform to optimize trades, compare token prices, and access top liquidity from multiple decentralized exchanges seamlessly."
      image={logoObj?.node?.gatsbyImageData?.images?.fallback?.src}
      noIndex={true}
    ></Seo>
  );
};

export default DefiDexDevelopment;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
    allImageSharp {
      edges {
        node {
          gatsbyImageData
          id
        }
      }
    }
  }
`;
